"use client";

import React from "react";
import { useGetProductsQuery } from "../features/apiSlice";

export default function Product() {
  const { data, error, isLoading } = useGetProductsQuery();

  if (isLoading)
    return <p className="text-center text-lg mt-20">Loading products...</p>;
  if (error)
    return (
      <p className="text-center text-red-500 mt-20">Failed to load products.</p>
    );

  return (
    <div
      className="w-full min-h-screen bg-cover bg-center bg-no-repeat py-24"
      style={{ backgroundImage: "url('/img/home2.jpg')" }}
    >
      <div className="max-w-6xl mx-auto px-4">
        {/* Title */}
        <div className="max-w-6xl mx-auto flex flex-col items-center px-4">
          <h1 className="text-5xl font-extrabold mb-6 text-black tracking-wide uppercase text-center">
            popular items
          </h1>

          <p className="text-black text-base mt-6 mb-12 max-w-xl text-center">
            Proin gravida nibh vel velit auctor aliquet. Aenean sollicitudin,
            lorem quis bibendum auctor, nisi elit.
          </p>
        </div>

        {/* Products Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
          {data?.map((prod, index) => (
            <div key={index} className="flex flex-col items-center">
              <img
                src={prod.img}
                alt={prod.title}
                className="w-full h-[420px] object-contain"
              />
              <h3 className="text-lg font-semibold text-black text-center mt-2">
                {prod.title}
              </h3>
              <p className="text-black  text-center">Price: ${prod.price}</p>
            </div>
          ))}
        </div>

        {/* Bottom spacing */}
        <div className="h-10" />
        <div className=" ">hi</div>
      </div>
    </div>
  );
}
